var _self;
export default {
	data() {
		return {
			mine: true,
			ewmImg: "https://s1.ax1x.com/2020/08/10/aHobY4.md.jpg",
			writer_name: "陈浩南",
			grade_cla: "三年四班",
			date_wri: "1999-7-9",
			quan: 324,
			titbits: [
				{
					ewmImg: "https://s1.ax1x.com/2020/08/10/ab8skd.md.jpg" //这是要保存的图片
				},
				{
					ewmImg: "https://s1.ax1x.com/2020/08/10/aHqPbt.md.jpg" //这是要保存的图片
				}
			],

			openSettingBtnHidden: true, //是否授权

		};
	},
	onLoad(opt) { 
	},
	components: {},
	methods: {
		downFile(url) {
			uni.downloadFile({ //下载文件资源到本地，客户端直接发起一个 HTTP GET 请求，返回文件的本地临时路径。
				url, //图片地址
				success: (res) => {
					console.log(res)
					if (res.statusCode === 200) {
						uni.saveImageToPhotosAlbum({  //保存图片到系统相册。
							filePath: res.tempFilePath,
							success: function() {
								uni.showToast({
									title: "保存成功",
									icon: "none"
								});
							},
							fail: function() {
								uni.showToast({
									title: "保存失败",
									icon: "none"
								});
							}
						});
					}
				}
			})
		},


		//微信小程序保存到相册
		handleSetting(e) {
			if (!e.detail.authSetting['scope.writePhotosAlbum']) {
				console.log(1);
				_self.openSettingBtnHidden = false;
			} else {
				console.log(2);
				_self.openSettingBtnHidden = true;
			}
			
		},
		
		saveEwm: function(e) {
			//获取相册授权
			uni.getSetting({ //获取用户的当前设置。
				success(res) {
					console.log(res)
					if (!res.authSetting['scope.writePhotosAlbum']) {
						
						// {uni.authorize}提前向用户发起授权请求。调用后会立刻弹窗询问用户是否同意授权小程序使用某项功能或获取用户的某些数据，
						// 但不会实际调用对应接口。如果用户之前已经同意授权，则不会出现弹窗，直接返回成功。如果用户之前拒绝了授权，
						// 此接口会直接进入失败回调，一般搭配uni.getSetting和uni.openSetting使用。
						uni.authorize({ 
							scope: 'scope.writePhotosAlbum',
							success() {
								//这里是用户同意授权后的回调
								_self.saveImgToLocal();
							},
							fail() { //这里是用户拒绝授权后的回调
							console.log('2222')
								_self.openSettingBtnHidden = false
							}
						})
					} else { //用户已经授权过了
					console.log('同意了')
						_self.saveImgToLocal();
					}
				}
			})
		},
		
		getSetting(){
			return new Promise((resolve,reject) => {
				uni.getSetting({ //获取用户的当前设置。
					success(res) {
						if (!res.authSetting['scope.writePhotosAlbum']) {
							_self.openSettingBtnHidden = true;
							resolve();
						} else { //用户已经授权过了
							_self.saveImgToLocal();
						}
					}
				})
			})
		},
		
		// 提示是否保存的模态框
		saveImgToLocal: function(e) {
			uni.showModal({ //显示消息提示框。
				title: '提示',
				content: '确定保存到相册吗',
				success: function(res) {
					if (res.confirm) {
						_self.downFile(_self.ewmImg)
						for (let i = 0; i < _self.titbits.length; i++) {
							_self.downFile(_self.titbits[i].ewmImg)
						}


					} else if (res.cancel) {

					}
				}
			});

		}
	},


}
